
.cass-knowledge-listing__grid {
    @extend .grid;

    > div {
        @include grid-column(4);
    }

    &.cass-knowledge-listing__grid--1 {
        > div {
            @include grid-column(6);
            @include grid-column-push(3);
        }
    }

    &.cass-knowledge-listing__grid--2 {
        > div {
            @include grid-column(6);
        }
    }
}

.cass-knowledge-listing__link {
    text-align: right;

    font-size: $font-size-small;
    font-weight: 600;

    a {
        @include anchor(skate);
    }

    .fa {
        margin-left: $measure-large;
    }
}

.cass-knowledge-listing__item {
    position: relative;
    margin-bottom: $measure-large;
}

$cass-knowledge-breakpoints: (
                ("min", 100%, 100%, false),
                ("small", 90%, 60%, true),
                ("between", 65%, 50%, true),
                ("medium", 100%, 100%, false),
                ("large", 95%, 55%, true),
                ("extralarge", 75%, 45%, true)
);

@each $bp in $cass-knowledge-breakpoints {
    @include media("above:#{nth($bp, 1)}") {
        .cass-knowledge-listing__item img {
            width: nth($bp, 2);
        }
        .cass-knowledge-listing__item__text {
            width: nth($bp, 3);
            @if (nth($bp, 4)) {
                position: absolute;
                display: inline-block;
            } @else {
                position: relative;
                display: block;
            }
        }
    }
}

.cass-knowledge-listing__item__text {
    background-color: white;
    top: 0;
    right: 0;
    padding: $measure-medium $measure-large $measure-large;
    box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.3);

    a {
        @include anchor(skate);

        font-family: $font-family-meta;
        font-size: $font-size-delta;
        font-weight: $font-weight-medium;
    }

    > a {
        margin-bottom: $measure-large;
    }
}

.cass-knowledge-listing__item__topic {
    @include brackets();

    $topics: (
                    (actuarial-science $actuarial-science),
                    (all $color-lucifer),
                    (finance $finance),
                    (management $management),
                    (entrepreneurship $entrepreneurship),
    );

    @each $topic in $topics {
        &.cass-knowledge-listing__item__topic--#{nth($topic, 1)} {
            @include brackets(nth($topic, 2));
        }
    }

    a {
        font-size: $font-size-small;
    }
}

.cass-knowledge-listing__item__research {
    position: relative;
    color: $color-emperor;
    margin-bottom: $measure-large;
    text-transform: uppercase;
    font-size: $font-size-small;
    font-weight: 600;

    &:before {
        position: absolute;
        content: "";
        top: 0;
        left: -$measure-large;
        width: 5px;
        height: 100%;
        background: black;
    }
}
