.feature-news {
    position: relative;
}

.feature-news__card {
    @include grid-column-map(
        (
            "small": 6,
            "large": 6
        )
    );
}

.feature-news__card:nth-child(2) {
    @include grid-column-map(
        (
            "small": 6,
            "large": 3
        )
    );
}

.feature-news__card:nth-child(3) {
    @include grid-column-map(
        (
            "small": 12,
            "large": 3
        )
    );
}

.feature-news__card--main,
.feature-news__card--follow {
    .feature-news__card__body {
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    }
}

.feature-news__card--main,
.feature-news__card--follow {
    img {
        width: 100%;
    }

    .feature-news__card__body {
        background-color: white;
        position: relative;
        top: -40px;
        width: 90%;
        padding: $measure-medium;
    }
}

.feature-news__card {
    a {
        @include anchor(skate);

        font-family: $font-family-meta;
        font-size: $font-size-delta;
        font-weight: $font-weight-medium;
    }
}

.feature-news__card--latest {
    padding-left: 20px;

    .feature-news__card__body {
        border-top: 1px solid $color-lucifer;

        @include media("above:medium") {
            padding: 20px 0 60px;
            border-bottom: 1px solid $color-lucifer;
        }

        &:before {
            content: "";
            display: block;
            width: 40px;
            height: 4px;
            background-color: $color-lucifer;
            position: relative;
            left: 0;
            top: -20px;
        }
    }
}

.feature-news__link-more {
    a {
        @include anchor(skate);
        font-weight: $font-weight-default;
    }

    position: relative;
    margin: 40px 0 0;
    float: right;

    @include media("above:large") {
        margin: 0;
    }
}

.item__date {
    font-size: $font-size-small;
}

.feature-news__card__body__topic {
    @include brackets();

    a {
        overflow: hidden;
        font-size: $font-size-small;
    }
}

.feature-news__card__body__topic--actuarial-science {
    @include brackets($actuarial-science);
}

.feature-news__card__body__topic--all {
    @include brackets($color-lucifer);
}

.feature-news__card__body__topic--finance {
    @include brackets($finance);
}

.feature-news__card__body__topic--management {
    @include brackets($management);
}

.feature-news__card__body__topic--entrepreneurship {
    @include brackets($entrepreneurship);
}

.feature-news__card__body__topic--cass-community {
    @include brackets($cass-community);
}

.feature-news__card--latest > .feature-news__card__body:not(.feature-news__card__body--slider-dots) {
    .feature-news__card__slider__item:not(:first-child) {
        display: none;
    }
}

.feature-news__card--latest > .feature-news__card__body.feature-news__card__body--slider-dots {
    position: relative;

    &:before {
        display: none;
    }

    .slick-dots {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 0;

        display: flex;
        flex-direction: row;

        > li {
            flex: 1;
            display: inline-block;
            background-color: transparent;
            height: auto;

            button {
                display: block;
                width: 100%;
                height: 4px;
                padding: 0;
                margin: 0;
                border: none;

                background-color: transparent;
                &:hover {
                    background-color: #aaaaaa;
                }
            }
            &.slick-active button {
                background-color: #666666;
            }
        }
    }
}
