// ////
/// Fonts
/// @group base
////


/// @type Map
/// @prop {String} family - Font Family
/// @prop {String} name - Font Name
/// @prop {String} style - Font Style
/// @prop {Number} weight - Font Weight
/// @prop {String} filename - Font Filepath and Filename
$font-families-styles: (
    "FF Meta": (
        ("Light",         normal, 300, "ff-meta/ff-meta-w03-light"),
        ("Normal",        normal, 400, "ff-meta/ff-meta-w03-normal"),
        ("Book",          normal, 500, "ff-meta/ff-meta-w03-book"),
        ("Medium",        normal, 600, "ff-meta/ff-meta-w03-medium"),
        ("Bold",          normal, 700, "ff-meta/ff-meta-w03-bold"),
        ("Light Italic",  italic, 300, "ff-meta/ff-meta-w03-light-italic"),
        ("Normal Italic", italic, 400, "ff-meta/ff-meta-w03-normal-italic"),
        ("Book Italic",   italic, 500, "ff-meta/ff-meta-w03-book-italic"),
        ("Medium Italic", italic, 600, "ff-meta/ff-meta-w03-medium-italic"),
        ("Bold Italic",   italic, 700, "ff-meta/ff-meta-w03-bold-italic")
    ),
    "PT Sans": (
        ("Regular",     normal, 400, "pt-sans/PT-Sans-regular"),
        ("Bold",        normal, 700, "pt-sans/PT-Sans-700"),
        ("Italic",      italic, 400, "pt-sans/PT-Sans-italic"),
        ("Bold Italic", italic, 700, "pt-sans/PT-Sans-700italic")
    )
);

/// @type Map
/// @prop {String} family - Font Family
/// @prop {List} types - Font File Types
$font-families-types: (
    "FF Meta":   (woff2, woff),
    "PT Sans":   (woff2, woff)
);

@each $family, $styles in $font-families-styles {
    @each $style in $styles {
        $src: ();
        $src: append($src, local("#{$family} #{nth($style, 1)}"));
        @each $type in map-get($font-families-types, $family) {
            $url: asset-url("/fonts/#{nth($style, 4)}.#{$type}") format("#{$type}");
            $src: append($src, $url, comma);
        }

        @font-face {
            font-family: $family;
            font-style: nth($style, 2);
            // stylelint-disable font-weight-notation
            font-weight: nth($style, 3);
            // stylelint-enable font-weight-notation
            // font-display: $font-display;
            src: #{$src};
            unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
        }
    }
}

$fa-font-path: "../fonts/fa4";
@import "../../node_modules/font-awesome/scss/font-awesome";
@import "../generated/icons.generated";
