////
/// Standfirst
/// @group components
////


.standfirst {
    @include font-family("meta");
    color: $color-emperor;
    font-size: $font-size-gamma;
    font-weight: $font-weight-light;
    line-height: $line-height-perfect-fifth;

    @include media("above:medium") {
        text-align: center;
    }
}
