@keyframes move {
    from {
      opacity: 0;
      transform: translateY(0);
    }
    
    to {
      opacity: 1;
      transform: translateY(-2rem);
    }
}

.carousel {
    position: relative;
}

.carousel__control {
    position: absolute;
    top: 35%;
    right: 0;
    width: 30px;
    z-index: 2;
}

.carousel__control__button {
    a {
        display: block;
        border-radius: 50%;
        width: 16px;
        height: 16px;
        margin-bottom: 24px;
        &:before {
            content: '';
            position: absolute;
            left: -6px;
            top: -6px;
            transition: all 0.2s ease;
            border-radius: 50%;
            bottom: 0;
            width: 28px;
            height: 28px;
        }
        @include anchor(control-button);
    }
}

.carousel__item {
    position: relative;
    display: none;
}

.carousel__item:first-of-type {
    display: block;
}

.carousel__item__info {
    position: absolute;
    top:  0;
    left: 0;
    max-width: 300px;
}

.carousel__item__info__overline {
    text-transform: uppercase;
    color: $color-white;
    font-weight: $font-weight-bold;
    animation: move 0.5s ease-in forwards;

    &:after {
        content: '';
        height: 5px;
        width: 80px;
        display: inline-block;
        background-color: $color-nobel;
        position: relative;
        bottom: 3px;
        margin-left: $measure-small;
    }
}

.carousel__item__info__body {
    color: white;
    font-size: $font-size-alpha;
    @include font-family(meta);
    font-weight: $font-weight-bold;
    margin-top: $measure-medium;
    animation:move 1s ease-in forwards;
}

.carousel__item__info__cta {
    @include anchor(cta);
    @include anchor(cta--black);
    text-align: left ;
    padding: $measure-small;
    margin-top: $measure-large;
    animation:move 1.5s ease-in forwards;
    span {
        position: absolute;
        right: $measure-small;
    }
}

.explore-theme {
    .carousel__item__info {
        top: $measure-large;
        padding-top: $measure-large;

        @include media('above:small') {
            top: $measure-enormous;
            padding-top: $measure-enormous;
        }
    }
}
