///
// Left-Hand Navigation
// @group components
///


$level-colors:
    #f2f2f2,
    #e5e5e5,
    #d8d8d8,
    #cbcbcb,
    #bebebe,
    #a1a1a1;


.left-hand-navigation {
    max-height: 100vh;
    @include box-shadow(beta, rgba($color-black, $opacity-fifth));
    margin-top: $measure-small;
    margin-bottom: $measure-medium;
    font-size: $font-size-small;
    z-index: z-index(above);
    position: sticky;
    align-self: flex-start;
    top: 0;

    h2 {
        background-color: $color-lightgray;
        margin: 0;
        padding: $measure-small $measure-medium;
        font-size: $font-size-default;
        font-weight: $font-weight-bold;
    }

    @include media("below:medium") {
        display: none;
    }

    &.left-hand-navigation--top {
        top: 0;
    }

    &.left-hand-navigation--bottom {
        bottom: 0;
    }

    &.left-hand-navigation--absolute {
        width: calc(100% - #{$measure-medium});
        position: absolute;
    }

    &.left-hand-navigation--fixed {
        position: fixed;

        @include media("below:large") {
            width: calc(#{grid-column-width(3)} - #{$measure-medium} - #{$measure-small / 2});
        }

        @include media("above:large", "below:extralarge") {
            width: calc(#{strip-units(grid-column-width(10)) / 100 * grid-column-width(3)} - #{$measure-medium} - #{$measure-small / 2});
        }

        @include media("above:extralarge") {
            width: calc((#{$grid-width-extra-large} - #{$measure-medium}) * #{strip-units(grid-column-width(10)) / 100} * #{strip-units(grid-column-width(3)) / 100} - #{$measure-medium});
        }
    }
}

.left-hand-navigation > .left-hand-navigation__list {
    margin-right: $measure-medium;
}

.left-hand-navigation-grid {
    @include grid;

    > div {
        @include grid-column-map(("small": 12, "large": 10));
        @include grid-column-push-map(("small": 0, "large": 1));

        > div {
            @include grid;
        }
    }
}

.left-hand-navigation-grid__nav {
    @include grid-column(3);
    margin-bottom: $border-width-hairline;
}

.left-hand-navigation-grid__body {
    @include grid-column(7);
    @include grid-column-push(1);

    @include media("below:medium") {
        overflow-x: hidden;
    }
}

.left-hand-navigation__menu > div > ul {
    max-height: calc(100vh - #{$font-size-default * $line-height-perfect-fifth + $measure-small * 2 + $measure-small + $measure-medium});
    margin: 0;
    overflow-y: auto;
}

.left-hand-navigation__menu .vertical-menu__header {
    a {
        border: $border-width-default solid transparent;
        &:focus {
            border: $border-width-default solid $color-black;
        }
        &:active {
            border: $border-width-default dotted $color-black;
        }
    }
    .vertical-menu__page-link {
        &:focus {
            outline: 0;
        }
    }
    .vertical-menu__toggle-link {
        &:focus {
            outline: 0;
        }
        &:active {
            outline: 0;
        }
    }
}


.left-hand-navigation--jsinit {
    h2 {
        display: none;
    }

    .vertical-menu--l2 > .vertical-menu__header > .vertical-menu__page-link {
        background-color: $color-lightgray;
        margin: 0;
        padding: $measure-small $measure-medium;
        font-family: $font-family-meta;
        font-size: $font-size-default;
        font-weight: $font-weight-bold;
        line-height: $line-height-perfect-fifth;
    }
}


