.chart-row__items__chart__row__legend__item {
    margin-bottom: 0.5rem;
    font-family: "FF Meta", "Helvetica Neue", Arial, Helvetica, sans-serif;
    font-weight: 700;
    font-size: 1.2rem;
}

.chart-row__items__chart__row__legend__item:not(:last-child) {
    padding-bottom: 0.5rem;
    position: relative;
}

.chart-row__items__chart__row__legend__item:not(:last-child):after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 1rem;
    background: #d0cfcb;
    height: 1px;
    width: 50%;
}

.chart-row__items__chart__row__legend__item__label {
    margin-left: 1rem;
    color: #555555;
    font-size: 1rem;
    font-weight: 400;
}

table {
    overflow: hidden;
    position: relative;
    z-index: 0;
    width: auto;
    max-width: 100%;
    margin: 0.5rem auto;
    border-collapse: collapse;
}

table td,
table th {
    padding: 10px;
    position: relative;
    outline: 0;
    text-align: center;
    border-bottom: 1px solid #e5e5e5;
    vertical-align: top;
    border-left: 0;
    border-right: 0;
    margin: 0;
}

table th {
    font-weight: 700;
}

table tr {
    position: relative;
    border-left: 0;
    border-right: 0;
    margin: 0;
    padding: 0;
}

table thead th {
    border-bottom-width: 2px;
}

table tbody th {
    text-align: left;
    white-space: nowrap;
}

table tbody > tr:hover td,
table tbody > tr:hover th {
    background: #f2f2f2;
}

table td:hover:after,
table thead th:not(:empty):hover:after {
    content: "";
    position: absolute;
    z-index: -2;
    top: -5000px;
    left: 0;
    width: 100%;
    height: 10000px;
    background: #f2f2f2;
}

table tbody > tr > td:hover {
    background: #d8d8d8;
}

.timeline {
    position: relative;
    margin-bottom: 3rem;
}

.timeline:before {
    content: "";
    position: absolute;
    top: 0;
    height: 100%;
    width: 4px;
    background: black;
    left: 50%;
}

@media (max-width: 500px) {
    .timeline:before {
        left: 10%;
    }
}

.timeline .timeline__collection {
    width: 100%;
}

@media (max-width: 500px) {
    .timeline .timeline__collection.bounce-in {
        animation: timeline-bounce-mobile 1s;
    }
}

@media (min-width: 501px) {
    .timeline .timeline__collection.bounce-in:nth-child(odd) {
        animation: timeline-bounce 0.6s;
    }
}

.timeline .timeline__collection .timeline__collection__icon {
    position: absolute;
    background-color: #d0cfcb;
    width: 60px;
    height: 60px;
    left: 50%;
    margin-left: -28px;
    border-radius: 50%;
}

@media (max-width: 500px) {
    .timeline .timeline__collection .timeline__collection__icon {
        width: 40px;
        height: 40px;
        left: 10%;
        margin-left: -18px;
    }
}

.timeline .timeline__collection .timeline__collection__icon span {
    color: #ffffff;
    font-size: 30px;
    position: relative;
    left: 16px;
    top: 15px;
}

@media (max-width: 500px) {
    .timeline .timeline__collection .timeline__collection__icon span {
        font-size: 20px;
        left: 10px;
        top: 10px;
    }
}

.timeline .timeline__collection .timeline__collection__content {
    width: 45%;
    padding: 0 10px 5px 0;
    text-align: right;
}

@media (max-width: 500px) {
    .timeline .timeline__collection .timeline__collection__content {
        position: relative;
        left: 20%;
        width: 80%;
        text-align: left;
    }
}

@media (min-width: 501px) {
    .timeline .timeline__collection.bounce-in:nth-child(even) {
        animation: timeline-bounce-inverse 0.6s;
    }
}

.timeline .timeline__collection:nth-child(even) .timeline__collection__content {
    position: relative;
    text-align: left;
    left: 55%;
    padding: 0 0 5px 10px;
}

@media (max-width: 500px) {
    .timeline .timeline__collection:nth-child(even) .timeline__collection__content {
        left: 20%;
        padding: 0;
    }
}

.timeline .timeline__collection--hide {
    visibility: hidden;
}

.scp--masters .timeline__collection .timeline__collection__icon {
    background-color: #0077a0;
}

.scp--undergraduate .timeline__collection .timeline__collection__icon {
    background-color: #fbae3b;
}

.scp--mba .timeline__collection .timeline__collection__icon {
    background-color: #88d1ce;
}

.scp--phd .timeline__collection .timeline__collection__icon {
    background-color: #49c7ed;
}

.scp--executive-education .timeline__collection .timeline__collection__icon {
    background-color: #ee3780;
}

@keyframes timeline-bounce-inverse {
    0% {
        opacity: 0;
        -webkit-transform: translateX(100px);
        -moz-transform: translateX(100px);
        -ms-transform: translateX(100px);
        -o-transform: translateX(100px);
        transform: translateX(100px);
    }
    60% {
        opacity: 1;
        -webkit-transform: translateX(-20px);
        -moz-transform: translateX(-20px);
        -ms-transform: translateX(-20px);
        -o-transform: translateX(-20px);
        transform: translateX(-20px);
    }
    100% {
        -webkit-transform: translateX(0);
        -moz-transform: translateX(0);
        -ms-transform: translateX(0);
        -o-transform: translateX(0);
        transform: translateX(0);
    }
}

@keyframes timeline-bounce {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-100px);
        -moz-transform: translateX(-100px);
        -ms-transform: translateX(-100px);
        -o-transform: translateX(-100px);
        transform: translateX(-100px);
    }
    60% {
        opacity: 1;
        -webkit-transform: translateX(20px);
        -moz-transform: translateX(20px);
        -ms-transform: translateX(20px);
        -o-transform: translateX(20px);
        transform: translateX(20px);
    }
    100% {
        -webkit-transform: translateX(0);
        -moz-transform: translateX(0);
        -ms-transform: translateX(0);
        -o-transform: translateX(0);
        transform: translateX(0);
    }
}

@keyframes timeline-bounce-mobile {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.link__external:after {
    font-family: "FontAwesome";
    content: "\f08e";
    padding-left: 0.3rem;
    font-size: 12px;
}

.link__pdf::before {
    font-family: "FontAwesome";
    font-size: 1rem;
    content: "\f1c1";
    padding-right: 0.5rem;
}

.mba-popup {
    align-items: center;
    background: rgba(30, 30, 30, 0.7);
    display: none !important;
    height: 100%;
    justify-content: center;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 4;
}

.mba-popup h2 {
    max-width: 70%;
}

.mba-popup.open {
    display: flex !important;
}

.mba-popup__form {
    background-color: #ffffff;
    display: inline-block;
    flex: auto;
    height: auto;
    max-height: calc(100% - 1rem);
    max-width: calc(100% - 1rem);
    overflow-y: auto;
    position: relative;
    width: auto;
}

@media (min-width: 901px) {
    .mba-popup__form {
        max-height: 70%;
        max-width: 50%;
    }
}

@media (min-width: 501px) {
    .mba-popup__form {
        padding: 2rem;
    }
}

@media (max-width: 500px) {
    .mba-popup__form {
        padding: 2rem 1rem 2rem;
    }
}

.mba-popup__form__close {
    position: absolute;
    background-color: #d92231;
    color: #ffffff;
}

@media (min-width: 501px) {
    .mba-popup__form__close {
        right: 2rem;
        top: 2rem;
    }
}

@media (max-width: 500px) {
    .mba-popup__form__close {
        right: 1rem;
        top: 2rem;
    }
}

.mba-popup__form__close:hover, .mba-popup__form__close:focus, .mba-popup__form__close:active {
    background-color: #bf1e2b;
    color: #ffffff;
}

.student-profiles-search {
    padding-bottom: 2rem;
}

.student-profiles-search .student-profiles-search__top .student-profiles-search__filters {
    display: flex;
    justify-content: space-evenly;
    padding: 1rem;
}

@media (max-width: 500px) {
    .student-profiles-search .student-profiles-search__top .student-profiles-search__filters {
        flex-direction: column;
    }
}

.student-profiles-search .student-profiles-search__top .student-profiles-search__filters .filter--box {
    width: 30%;
    margin: 0 0.5rem;
    position: relative;
}

@media (max-width: 500px) {
    .student-profiles-search .student-profiles-search__top .student-profiles-search__filters .filter--box {
        width: 100%;
        margin-bottom: 1rem;
    }
}

.student-profiles-search .student-profiles-search__top .student-profiles-search__filters .filter--box select {
    width: 100%;
    padding: 3px 20px 3px 6px;
    height: 42px;
