    background-color: inherit;
    color: inherit;
    border-bottom-width: 0;
    background-color: #a1a1a1;
}

.carousel__control__button a:before {
    content: '';
    position: absolute;
    left: -6px;
    top: -6px;
    transition: all 0.2s ease;
    border-radius: 50%;
    bottom: 0;
    width: 28px;
    height: 28px;
