.student-profiles-search{
    padding-bottom: 2rem;

    .student-profiles-search__top{
        .student-profiles-search__filters{
            display: flex;
            justify-content: space-evenly;
            padding: 1rem;

            @include media("below:small") {
                flex-direction: column;
            }

            .filter--box{
                width: 30%;
                margin: 0 0.5rem;
                position: relative;
                @include media("below:small") {
                    width:100%;
                    margin-bottom: 1rem;
                }
                @include clear-dropdown-menu();
            }
        }

        .student-profiles-search__loadingSpinner{
            text-align: center;
        }

        .student-profiles-search__filters-wrap{
            background-color: $color-gallery;
            padding: 1rem;
            margin-bottom: 1rem;


            a{
                @include anchor(cta);
                @include anchor(cta--small);
                @include anchor(cta--sherpa);
                display: inline-block;
                padding: 0.5rem;
                margin-left: 0.3rem;
                border:2px solid #000000;
                margin-top: 6px;

                @include media("below:small") {
                    display: block;
                    margin-bottom: 0.3rem;
                }
                span{
                    color: $color-white;
                    border: none;
                    font-weight: 700;
                    text-transform: capitalize;

                    @include media("below:small") {
                        display: block;
                    }
                    svg{
                        display: inline;
                        max-height: 18px;

                        @include media("below:small") {
                            position: absolute;
                            right: 10px;
                        }
                    }
                    &:hover{
                        background-color: transparent !important;
                        cursor: pointer;
                    }
                    .clear-filter-icon{
                        display: inline-block;
                    }
                }
            }
        }

        .student-profiles{
            .student-profiles__profile{
                display: flex;
                flex-direction: row;
                float:left;
                min-height: 185px;
                position: relative;
                padding: 1rem;
                margin-bottom: 1rem;
                width: 47%;
                background-color: #fafafa;
                box-shadow: 0 8px 16px 0 rgba(30, 30, 30, 0.2);
                -webkit-box-shadow: 0 8px 16px 0 rgba(30, 30, 30, 0.2);

                &:nth-child(even){
                    margin-left: calc(100% - 94%);

                    @include media("below:small"){
                        margin-left: 0;
                    }
                }

                &:nth-child(odd){
                    clear: left;
                }

                @include media("below:small"){
                    width: 100%;
                }

                .student-profiles__profile__img{

                    @include media("below:small"){
                        width:35%;
                    }
                    a img{
                        max-height: 150px;
                        max-width: 100px;
                    }

                    a svg{
                        font-size: 100px;
                        color: $color-gainsboro;
                    }
                }

                .student-profiles__profile__details{
                    padding-left: 10px;
                    .student-profiles__profile__details__title{
                        margin-bottom: 1rem;
                    }
                    p{
                        margin: 0;
                    }
                    .firstLetterCap{
                        font-weight: 600;
                    }
                }
            }
        }
    }

    .student-profiles-search__bottom{
        clear: left;

        .loadMoreButtonContainer{
            @supports (display: flex) {
                display: flex;
                justify-content:center;
            }
            margin: auto;
            width: 50%;
            button{
                @include anchor(cta);
                @include anchor(cta--small);
                @include anchor(cta--white);
                padding: 10px 60px;
                font-size: 16px;
                font-weight: 600;
                margin: auto;
            }
        }

        .student-profiles-search__profileInfo{
            margin: auto;
            width:50%;
            margin-bottom: 0.7rem;
            padding-top: 16px;
            .student-profiles-search__profileInfo__text{
                text-align: center;
                margin-bottom: 0.7rem;
                font-size: 14px;
            }
            #student-profiles-search__profileInfo__wrap-progress-bar{
                height:3px;
                background-color: $color-lightgray;
                margin: auto;
                width: 50%;
                .progressbar-bar{
                    height: 3px;
                    background-color: $color-montecarlo;
                }
            }
        }
    }
}

.mba-student-profile{
    display: flex;
    padding: 1rem;
    border-bottom: 1px solid;
    margin-bottom: 1rem;
    .mba-student-profile__img{
        margin-right: 10px;
        img{
            max-height: 200px;
            max-width: 150px;
        }
    }
}