.timeline{
    position: relative;
    margin-bottom: $measure-xlarge;
    &:before{
        content: "";
        position: absolute;
        top: 0;
        height: 100%;
        width: 4px;
        background: black;
        left: 50%;
        @include media("below:small") {
            left:10%;
        }
    }

    .timeline__collection{
        width: 100%;

        &.bounce-in{
            @include media("below:small"){
                animation: timeline-bounce-mobile 1s;
            }
        }

        &.bounce-in:nth-child(odd){
            @include media("above:small"){
                animation: timeline-bounce 0.6s;
            }
        }

        .timeline__collection__icon{
            position: absolute;
            background-color: $color-quill;
            width: 60px;
            height: 60px;
            left: 50%;
            margin-left:-28px;
            border-radius: 50%;
            @include media("below:small") {
                width: 40px;
                height: 40px;
                left: 10%;
                margin-left: -18px;
            }

            span{
                color: #ffffff;
                font-size: 30px;
                position: relative;
                left: 16px;
                top: 15px;
                @include media("below:small"){
                    font-size: 20px;
                    left: 10px;
                    top: 10px;
                }
            }
        }

        .timeline__collection__content{
            width: 45%;
            padding: 0 10px 5px 0;
            text-align: right;
            @include media("below:small"){
                position: relative;
                left: 20%;
                width: 80%;
                text-align: left;
            }
        }

        &.bounce-in:nth-child(even){
            @include media("above:small"){
                animation: timeline-bounce-inverse 0.6s;
            }
        }

        &:nth-child(even) .timeline__collection__content{
            position: relative;
            text-align: left;
            left: 55%;
            padding: 0 0 5px 10px;
            @include media("below:small"){
                left: 20%;
                padding: 0;
            }
        }

    }
    .timeline__collection--hide{
        visibility: hidden;
    }
}

.scp--masters{
    .timeline__collection{
        .timeline__collection__icon{
            background-color: $color-allports;
        }
    }
}

.scp--undergraduate{
    .timeline__collection{
        .timeline__collection__icon{
            background-color: $color-yellow;
        }
    }
}

.scp--mba{
    .timeline__collection{
        .timeline__collection__icon{
            background-color: $color-montecarlo;
        }
    }
}

.scp--phd{
    .timeline__collection{
        .timeline__collection__icon{
            background-color: $color-picton;
        }
    }
}

.scp--executive-education{
    .timeline__collection{
        .timeline__collection__icon{
            background-color: $color-violet;
        }
    }
}


@keyframes timeline-bounce-inverse{
    0% {
        opacity: 0;
        -webkit-transform: translateX(100px);
        -moz-transform: translateX(100px);
        -ms-transform: translateX(100px);
        -o-transform: translateX(100px);
        transform: translateX(100px);
    }

    60% {
        opacity: 1;
        -webkit-transform: translateX(-20px);
        -moz-transform: translateX(-20px);
        -ms-transform: translateX(-20px);
        -o-transform: translateX(-20px);
        transform: translateX(-20px);
    }

    100% {
        -webkit-transform: translateX(0);
        -moz-transform: translateX(0);
        -ms-transform: translateX(0);
        -o-transform: translateX(0);
        transform: translateX(0);
    }
}

@keyframes timeline-bounce {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-100px);
        -moz-transform: translateX(-100px);
        -ms-transform: translateX(-100px);
        -o-transform: translateX(-100px);
        transform: translateX(-100px);
    }

    60% {
        opacity: 1;
        -webkit-transform: translateX(20px);
        -moz-transform: translateX(20px);
        -ms-transform: translateX(20px);
        -o-transform: translateX(20px);
        transform: translateX(20px);
    }

    100% {
        -webkit-transform: translateX(0);
        -moz-transform: translateX(0);
        -ms-transform: translateX(0);
        -o-transform: translateX(0);
        transform: translateX(0);
    }
}
@keyframes timeline-bounce-mobile {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
