////
/// Primary Navigation
/// @group components
////


.primary-navigation {
    @include grid-column(6, "small", true, true);
    @include grid-column(8, "medium", false, false);
    @include grid-column(7, "large", false, false);
    align-self: center;

    @include media("below:between") {
        display: none;
    }
}

.primary-navigation__list {
    width: 100%;
    display: flex;
    flex-direction: row;

    @include media("above:medium") {
        justify-content: space-between;
    }

    @include media("below:medium") {
        justify-content: center;
    }

    li {
        display: inline-block;

        &:nth-last-child(1),
        &:nth-last-child(2) {
            @include media("below:medium") {
                display: none;
            }
        }

        &:nth-last-child(3) {
            @include media("below:780px") {
                display: none;
            }
        }
    }

    li + li {
        @include media("below:medium") {
            margin-left: $measure-medium;
        }
    }

    a {
        @include anchor(primary-navigation);
    }
}

.explore-theme {
    .primary-navigation {
        .primary-navigation__list {
            a {
                @include anchor(primary-navigation--dark-bg);
            }
        }
    }
}
