////
/// Breadcrumbs
/// @group components
////


$chevron-padding-default: ($measure-medium * 3/4);
$chevron-padding-mobile: $measure-small;

.breadcrumbs {
    @include grid;
    background-color: $color-lightgray;
    box-shadow: inset 0 0px 8px 0 rgba(0,0,0,.15);
    font-size: $font-size-small;
    padding: $measure-small 0;

    @include media("above:medium") {
        display: none;
    }

    &:before {
        content: '';
        background: $color-lightgray;
        width: 200vw;
        height: 100%;
        position: absolute;
        transform: translate(-50%, -20%);
        box-shadow: inset 0 0px 8px 0 rgba(0,0,0,.15);
    }
}

.breadcrumbs__list {
    @include grid-column(12);

    li {
        display: inline-block;
        padding-top:    $border-width-hairline;
        padding-bottom: $border-width-hairline;
        padding-right: $chevron-padding-default;
    }

    li + li {
        position: relative;

        &:before {
            @include font-family(icon);
            content: "\f105";
            color: $color-lucifer;
            font-size: $font-size-default;
            padding-right: $chevron-padding-default;
        }
    }

    @include media("below:small") {
        li {
            padding-right: $chevron-padding-mobile;
        }

        li + li {
            &:before {
                padding-right: $chevron-padding-mobile;
            }
        }
    }
}
