////
/// Vertical split
/// @group components
////


.vertical-split {
    background-image: linear-gradient(to top, $color-concrete 50%, $color-white 50%);
}

.vertical-split--reverse {
    background-image: linear-gradient(to bottom, $color-concrete 50%, $color-white 50%);
}

.vertical-split--catskill-white {
    background-image: linear-gradient(to top, $color-catskill-white 50%, rgba(0,0,0,0) 50%);
}

.vertical-split--embedded {
    margin-top: $measure-enormous;
}
