////
/// Accordion Tabs
/// @group components
////

.accordion-tabs {
    @include media("below:small") {
        padding-left: $grid-padding;
        padding-right: $grid-padding;
        margin: 2rem 0;
    }
    .tabs__content__heading-anchor:hover:before, .tabs__content__heading-anchor[aria-selected="true"]:before {
        display: none;
    }
}

.accordion-tabs__content__group {
    overflow: hidden;
    margin: 0 auto;
    @include media("below:small") {
        border: 2px dotted #1e1e1e;
        border-top: 0;
    }
    .tabs__content__group {
        border-bottom: $border-width-hairline $border-style-alternate $color-nobel;
    }
    // Nested accordions
    .accordion {
        margin-top: 0;
        .accordion__content__heading-anchor__label {
            width: 85%;
            display: inline-block;
            color: $color-black;
        }
        .accordion__content__heading-anchor__icon {
            position: relative;
            left: $measure-medium;
            color: $color-black;
        }
    }
}

.accordion-tabs__content__group__shame {
    .accordion-tabs__content__heading-anchor.active + & {
        @include animation(slide-top-in);
    }
}

.js {
    .accordion-tabs__content__heading-anchor {
        &.inactive + div {
            @include visually-hidden;

            // required for tab key events
            display: none;
        }
    }

    .accordion-tabs__content__heading,
    .accordion-tabs__menu-fullwidth,
    .accordion-tabs__menu {
        @include media("below:small") {
            @include visually-hidden;
        }
    }

    .accordion-tabs__content__group > div {
        @include media("above:small") {
            padding-top: $measure-large;
            padding-bottom: $measure-large;
        }

        @include media("below:small") {
            margin-top: $measure-medium;
            margin-bottom: $measure-medium;
        }
    }

    .accordion-tabs__content__heading-anchor {
        margin: 0;
        @include media("above:small") {
            @include visually-hidden;
            &:not([aria-selected="true"]) {
                display: none;
            }
        }
    }

    .accordion-tabs__content__heading-anchor__icon {
        @extend .fa;
        @extend .fa-plus;
        float: right;
        left: $measure-medium;
        position: relative;
    }

    [aria-selected="true"] .accordion-tabs__content__heading-anchor__icon {
        @extend .fa-minus;
    }
}

.accordion-tabs__content__heading-anchor.active {
    color: #1e1e1e;
    background: #ffffff;
    border: 2px dotted #1e1e1e;
    border-bottom: 0;
    margin: 0 0 2px;
    color: #1e1e1e;
    text-decoration: underline;
    border-bottom: 1px dotted #ececec;
    &[aria-selected="true"] {
        background: #ffffff;
    }
    &:focus {
        outline: 3px solid transparent;
    }
    .accordion-tabs__content__heading-anchor__label {
        text-decoration: underline;
    }

}

.accordion-tabs__content__heading-anchor.inactive {
    &:focus {
        outline: 3px solid #1e1e1e;
        bottom: 3px;
    }
}

.accordion-tabs__content__heading {
    position: relative;
    text-align: center;
}

/** Layout code: manages content width for tab-menu and tab-content */
.accordion-tabs--top-level-layout {
    .accordion-tabs__menu-fullwidth {
        background-color: $color-white;
        // this element can become sticky with stickybits. For IE support, width: 100% seems required
        width: 100%;
        z-index: z-index(navigation);
    }

    .accordion-tabs__content__heading-anchor.active {
        background: $color-flamingo;
    }

    .tabs .tabs__content__heading-anchor {
        &.active {
            background-color: $color-yellow;
        }
        &.inactive {
            border-bottom: $border-width-hairline $border-style-alternate $color-nobel;
        }
        .tabs__content__heading-anchor__label {
            width: 85%;
            display: inline-block;
        }
    }

    .accordion-tabs__menu-wrapper {
        @include grid-top;
    }

    .accordion-tabs__menu-fullwidth {
        background-color: $color-apricot;
    }

    .accordion-tabs__menu-wrapper {
        justify-content: center;
    }

    // Mobile: Nested accordions inside mobile accordions
    .tabs .tabs__content__heading-anchor {
        border: 3px solid transparent;
        &.inactive {
            &:hover {
                background: $color-whisper;
            }
            &:focus {
                /* border: 3px solid #1e1e1e;
                bottom: 3px;
                outline: 0;
                margin: 0; */
                border: 3px solid #1e1e1e;
                outline: 0;
                margin: 0;
            }
        }
        &.active {
            border: 0;
            &:focus {
                outline: 3px solid #1e1e1e;
                margin: 0;
            }
        }
    }

    .accordion-tabs__content__heading-anchor {
        color: #1e1e1e;
        margin: 0;
        font-size: $font-size-delta;
        padding-left: $measure-medium;
        padding-right: $measure-medium;
        border-bottom: 1px solid #ececec;
        background: #ffffff;
        &:last-of-type {
            // border-bottom: 1px dotted #a1a1a1;
        }
        &:active  {
            // outline: 2px dotted #1e1e1e;
        }
        &:hover {
            background: $color-whisper;
            text-decoration: underline;
        }
        &:focus {
            /* outline: 0;
            margin-right: 0;
            bottom: 3px; */
        }

        .accordion-tabs__content__heading-anchor__icon {
            line-height: #{$font-size-delta * $line-height-default};
            font-size: $font-size-default;
            left: 0;
        }
    }

    .accordion-tabs__content__heading-anchor__label {
        display: inline-block;
        line-height: $header-line-height;
        font-weight: 600;
        &:hover {
            text-decoration: underline;
        }
    }

    .container {
        margin-top: 0;
        margin-bottom: 0;
        position: relative;
        padding: 0 1rem;
    }
}

// Non-programme pages, e.g. staff profiles
.accordion-tabs--top-level-layout.accordion-tabs--top-level-layout--gallery {
    .accordion-tabs__menu-fullwidth {
        background-color: $color-gallery;
    }

    .accordion-tabs__menu__item {
        @include anchor(activable-inline-featured--alizarin);
    }

    .accordion-tabs__content__heading-anchor.active {
        background-color: $color-alizarin;
        color: $color-white;
    }

    .accordion-tabs__content__heading-anchor {
        &:hover {
            // background: red;
        }
    }

    .accordion-tabs__content__group {
        .accordion {
            .accordion__content__heading-anchor {
                background: $color-concrete;
                &:hover {
                    background: $color-whisper;
                }
            }
            .accordion__content__heading-anchor.active {
                background: $color-quill;
                .accordion__content__heading-anchor__label, .accordion__content__heading-anchor__icon {
                    color: $color-black;
                }
            }
        }
    }

}

.accordion-tabs--top-level-layout.accordion-tabs--top-level-layout--catskill-white {
    .accordion-tabs__menu-fullwidth {
        background-color: $color-catskill-white;
    }

    .accordion-tabs__menu__item {
        @include anchor(activable-inline-featured--prussian);
    }

    .accordion-tabs__content__heading-anchor.active {
        background: $color-prussian;
        color: $color-white;
    }
    .accordion__content__heading-anchor__icon {
        position: relative;
        left: 0
    }
    .tabs .tabs__content__heading-anchor.active {
        background: $color-allports;
        .tabs__content__heading-anchor__label, .tabs__content__heading-anchor__icon {
            color: $color-white;
        }
    }
}

.accordion-tabs__menu {
    @include reset-font-size(".accordion-tabs__menu__item");
    text-align: center;
    padding-top: $measure-large;
}

.accordion-tabs__menu__item {
    @include anchor(activable-inline-featured);
}

.accordion-tabs__content__heading-anchor {
    @include anchor(activable-stacked-featured);
}
