@import "filter-box";
@import "pagination";

.course-finder__top {
    @extend .section--gallery;
    padding: $measure-large 0;
}

.course-finder__main {
    @extend .section--lightgray;
    padding: $measure-medium 0;
}

.course-finder__results__item {
    position: relative;
    background: $color-white;
    float: left;
    padding: $measure-medium;
    margin-bottom: $measure-medium;
    width: 100%;
    @include media("above:medium") {
        padding: $measure-large;
        margin-bottom: $measure-large;
    }

}
.course-finder__main__left {
    .course-finder__main:not(.course-finder__main--nofilters) & {
        @include grid-column-map(("small": 12, "between": 4));
    }

    .course-finder__main.course-finder__main--nofilters & {
        @include grid-column-map(("small": 12, "between": 2));
    }
}

.course-finder__main__right {
    @include grid-column-map(("small": 12, "between": 8));
}

.course-finder__results__item__left {
    @include grid-column-map(("small": 12, "medium": 3));
}

.course-finder__results__item__main {
    @include grid-column-map(("small": 12, "medium": 9));
}

.course-finder__results__item__bottom {
    @include grid-column-map(("small": 12, "medium": 9));
    @include grid-column-push-map(("small": 0, "medium": 3));
}

.course-finder__results__item__level {
    color: $color-emperor;
    font-weight: $font-weight-medium;
}

.course-finder__results__item__title {
    font-size: $font-size-delta;
}

.course-finder__results__item__award {
    margin-top: $measure-small;
}

.course-finder__results__item__info {
    display: none;
    float: left;
    margin-top: $measure-small;
    border-top: 2px solid $color-gallery;
    width: 100%;
    &.course-finder__results__item__info--open {
        display: block;
    }
}

.course-finder__results__item__description {
    margin: $measure-small 0 $measure-medium;
}

.course-finder__results__item__md {
    margin-bottom: 5px;

    > div {
        margin-left: 22px;
    }

    span {
        color: $color-emperor;
    }

    .fa {
        float: left;
        padding-top: 3px;
    }
}

.course-finder__results__item__md__label {
    font-weight: $font-weight-medium;
}

@include media("above:between") { // 600px
    .course-finder__results__item__info-toggle {
        display: none;
    }

    .course-finder__results__item__info {
        display: block;
    }
}


.course-finder__results__summary__refresh {
    display: inline-block;
    color: $color-alizarin;
    margin-left: 5px;
}

.fade-out {
    opacity: 0;
    pointer-events: none;
    user-select: none;
    animation: fade-out 1s linear;
}

.course-finder__results__item__info-toggle {
    @include media("above:between") {
        display: none;
    }

    position: absolute;
    top: 10px;
    right: 10px;


    @include anchor(cta);
    @include anchor(cta--red);
    padding: $measure-small $measure-medium;

    .fa {
        transition: transform 300ms;
    }
}


.course-finder__top {
    .query-box {
        width: 100%;
        padding-right: 44px;
        position: relative;
    }
    input {
        padding: 13px 10px 12px;
        width: 100%;
        border: 1px solid $color-nobel;
        font-size: $font-size-default;
    }
    button {
        position: absolute;
        top: 0;
        right: 0;
        width: 45px;
        height: 45px;
        background: white;
        border: 1px solid $color-nobel;
    }
}

.course-finder__clear-buttons {
    @include media("below:between") {
        display: none;
    }

    > div {
        display: inline-block;
    }

    a {
        @include anchor(cta);
        @include anchor(cta--small);
        @include anchor(cta--red);

        margin: 10px 10px 0 0;
    }
}
